import { MessageTreeNode } from './treeNode';

import nucleaire from '../assets/nucleaire.json';
import lorem from '../assets/lorem.json';
import empty from '../assets/empty.json';

const trees = [
  nucleaire,
  lorem,
  empty
];

const MODELS = trees.length;

function buildTree(treeObject) {
  let tree = new MessageTreeNode(treeObject.title, treeObject.text, treeObject.type);
  treeObject.branchs.forEach(b => {
    tree.addBranch(buildTree(b));
  });

  return tree;
}

function getTree(i) {
  return buildTree(trees[i].tree);
}

function getInfo(i) {
  const { title, date } = trees[i];
  return {
    title,
    date
  };
}

export {
  getTree,
  getInfo,
  MODELS
}


