import React, { Component } from 'react';

import { MESSAGE, CARD, COLORS, MESSAGE_COLORS, LINES } from '../helpers/constants';

import addIcon from '../assets/ic_add.svg';
import actionIcon from '../assets/ic_build.svg';
import decisionIcon from '../assets/ic_gavel.svg';

const styles = {
  container: {
    boxSizing: 'border-box',
    height: CARD.buttonSize,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  button: {
    backgroundColor: COLORS.white,
    margin: '0 5px',
    borderRadius: '50%',
    border: LINES.border,
    cursor: 'pointer',
    width: CARD.buttonSize,
    height: CARD.buttonSize,
    padding: 0
  },
  icon: {
    margin: (CARD.buttonSize - CARD.buttonIconSize) / 2
  },
  bigIcon: {
    margin: (CARD.buttonSize - CARD.bigButtonIconSize) / 2
  }
}


function ActionButton(props) {
  let buttonStyle = Object.assign({}, styles.button);
  if (Object.hasOwnProperty.call(props, 'color')) {
    buttonStyle.backgroundColor = props.color;
  }
  return (
    <div style={buttonStyle} className='tooltip'>
      <img
        onClick={props.action}
        width={props.big ? CARD.bigButtonIconSize : CARD.buttonIconSize}
        src={props.icon}
        alt={props.alt}
        style={props.big ? styles.bigIcon : styles.icon}
      />
      <span className="tooltiptext">{props.alt}</span>
    </div>
  );
}

class MessageActions extends Component {
  render() {
    return (
      <div style={styles.container}>
        <ActionButton
          action={() => {this.props.add(MESSAGE.DECISION);}}
          alt="Add decision"
          icon={decisionIcon}
          color={MESSAGE_COLORS[MESSAGE.DECISION]}
        />
        <ActionButton
          action={() => {this.props.add(MESSAGE.ANSWER);}}
          alt="Add answer"
          icon={addIcon}
          big
        />
        <ActionButton
          action={() => {this.props.add(MESSAGE.ACTION);}}
          alt="Add action"
          icon={actionIcon}
          color={MESSAGE_COLORS[MESSAGE.ACTION]}
        />
      </div>
    );
  }
}

export default MessageActions;


