import React, { Component } from 'react';

import Grid from './Grid';

import { getTree, getInfo, MODELS } from '../helpers/buildTree';
import { COLORS, LINES, HEADER } from '../helpers/constants';

const zoomLevels = [0.5, 0.8, 1, 1.3];

const styles = {
  container: {
    textAlign: 'center',
    width: 'fit-content'
  },
  header: {
    backgroundColor: COLORS.accent,
    boxSizing: 'border-box',
    height: HEADER.height,
    borderBottom: LINES.border,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    position: 'fixed',
    top: 0,
    zIndex: 2,
    width: '100%'
  },
  zoom: {
    position: 'fixed',
    bottom: 0,
    right: 0,
    display: 'flex',
    flexDirection: 'column'
  },
  app: {
    padding: '0 40px 40px',
    transformOrigin: `left ${HEADER.height}px`
  },
  button: {
    backgroundColor: COLORS.white,
    border: LINES.border,
    borderRadius: '50%',
    height: HEADER.buttons,
    width: HEADER.buttons,
    margin: (HEADER.height - HEADER.buttons) / 2,
    cursor: 'pointer',
    color: COLORS.black
  },
  selectedButton: {
    backgroundColor: COLORS.accent,
    color: COLORS.white,
    border: LINES.whiteBorder,
  },
  logo: {
    padding: (HEADER.height - HEADER.logo) / 2,
  },
  title: {
    color: COLORS.white,
    fontSize: HEADER.title
  },
  zoomButton: {
    marginTop: 0,
    fontSize: 25
  }
}

const selectedButtonStyle = { ...styles.button, ...styles.selectedButton };
const zoomButtonStyle = { ...styles.button, ...styles.zoomButton };

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedTree: 0,
      zoomLevel: 2,
      tree: getTree(0)
    }
  }

  selectTree(selectedTree) {
    if (selectedTree !== this.state.selectedTree) {
      this.setState({
        selectedTree,
        tree: getTree(selectedTree)
      });
    }
  }

  zoom(more) {
    if(more) {
      if (this.state.zoomLevel < 3) {
        this.setState({
          zoomLevel: this.state.zoomLevel + 1
        });
      }
    } else {
      if (this.state.zoomLevel > 0) {
        this.setState({
          zoomLevel: this.state.zoomLevel - 1
        });
      }
    }
  }

  render() {
    const info = getInfo(this.state.selectedTree);

    const buttons = [];
    for (let i=0; i<MODELS; i++) {
      buttons.push(
        <button
          key={i}
          style={(this.state.selectedTree === i) ? selectedButtonStyle : styles.button}
          onClick={() => {this.selectTree(i);}}
        >{i + 1}</button>
      )
    }

    return (
      <div style={styles.container}>
        <div style={styles.header}>
          <img src='logo.png' alt='Logo' height={HEADER.logo} style={styles.logo}/>
          <div style={styles.title}>{`Réunion "${info.title}" du ${info.date}`}</div>
          <div>
            {buttons}
          </div>
        </div>
        <div style={{ ...styles.app, transform: `scale(${zoomLevels[this.state.zoomLevel]})` }}>
          <Grid
            tree={this.state.tree}
            usedSpace={0}
            root
          />
        </div>
        <div style={styles.zoom}>
          <button
            style={zoomButtonStyle}
            onClick={() => {this.zoom(true)}}
          >+</button>
          <button
            style={zoomButtonStyle}
            onClick={() => {this.zoom(false)}}
          >-</button>
        </div>
      </div>
    );
  }
}

export default App;



