import { CARD, GRID } from './constants';

const horizontalLengthFactor = CARD.boxWidth + GRID.columnGap;
const verticalLength = GRID.rowGap;

function branchLength(relativePos) {  
  const horizontalLength = relativePos * horizontalLengthFactor;

  return Math.sqrt(square(horizontalLength) + square(verticalLength));
}

function branchAngle(relativePos) {
  if (relativePos === 0) {
    return 0;
  }
  const horizontalLength = relativePos * horizontalLengthFactor;  

  const angle = radToDeg(Math.atan(verticalLength / horizontalLength));
  return angle > 0 ? angle - 90 : angle + 90;
}

function square(nb) {
  return nb * nb;
}

function radToDeg(rad) {
  return (rad * 57.2958);
}

export {
  branchLength,
  branchAngle
};


