import React, { Component } from 'react';

import Message from './Message';

import { GRID, MESSAGE_COLORS, TOOLTIP } from '../helpers/constants';

import { MessageTreeNode } from '../helpers/treeNode';

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
    flexWrap: 'nowrap',
    alignItems: 'center'
  },
  margins: {
    margin: `${GRID.rowGap}px ${GRID.columnGap / 2}px 0`,
  },
  branchs: {
    display: 'flex',
    flexWrap: 'nowrap'
  }
};

function flexConfig(width) {
  return {
    flexGrow: width
  };
}

function addChildTo(node) {
  return type => {
    const newNode = new MessageTreeNode("Titre", "...", type);
    node.addBranch(newNode);
  };
}

class Grid extends Component {
  constructor(props) {
    super(props);

    if (Object.hasOwnProperty.call(props, 'update')) {
      this.update = props.update;
    } else {
      this.update = () => {
        this.forceUpdate();
      }
    }
  }

  render() {
    const n = this.props.tree;
    const addChild = addChildTo(n);
    let usedBranchSpace = 0;
    const position = this.props.root ? 0 : this.props.usedSpace + (n.width / 2) - (this.props.fatherWidth / 2);

    return (
      <div style={{ ...styles.container, ...flexConfig(n.width) }}>
        <div style={styles.margins}>
          <Message
            title={n.title}
            text={n.message}
            position={position}
            color={MESSAGE_COLORS[n.type]}
            tooltip={TOOLTIP[n.type]}
            addChild={type => {
              addChild(type);
              this.update();
            }}
          />
        </div>
        <div style={styles.branchs}>
          {
            n.branchs.map((branch, idx) => {
              const bs = usedBranchSpace;
              usedBranchSpace += branch.width;
              return (
                <Grid
                  tree={branch}
                  key={idx}
                  usedSpace={bs}
                  fatherWidth={n.width}
                  update={this.update}
                />
              );
            })
          }
        </div>
      </div>
    );
  }
}

export default Grid;


