import { MESSAGE } from './constants';

class TreeNode {
  constructor(data = {}, branchs = []) {
    this.data = data;
    this.calculatedWidth = -1;
    this.calculatedHeight = -1;
    this.father = null;

    this.branchs = branchs;
    this.branchs.forEach(node => {
      node.father = this;
    })

    // Generator for DFS in the tree. x and y are the coordinates of the root (this)
    // /!\ x is inherited from the father node, and also depends onth width of sister branchs:
    //
    // x: 1 2 3 4 5            |  x: 1 2 3 4 5
    //    * <------(x=1)       |     * <------(x=1)
    //    |\                   |     | \
    //    * *                  |     *   * <--(x=3)
    //      |\                 |     |\
    //      * * <--(x=3)       |     * * <----(x=2)
    //
    this.dfsGen = (function*(x = 0, y = 0) {
      yield {node: this, x, y};
      let branchWidth = 0;
      for (let i=0; i < this.branchs.length; i++) {
        yield* this.branchs[i].dfsGen(x+branchWidth, y+1);
        branchWidth += this.branchs[i].width;
      }
    });
  }

  resetSize() {
    this.calculatedWidth = -1;
    this.calculatedHeight = -1;
    
    if (this.father !== null) {
      this.father.resetSize();
    }
  }

  addBranch(...newBranchs) {
    newBranchs.forEach(node => {
      node.father = this;
      this.branchs.push(node);
    });
    this.resetSize();
  }

  // Lazy evaluation
  get width() {
    if (this.calculatedWidth !== -1) {
      return this.calculatedWidth;
    }

    if (this.branchs.length === 0) {
      this.calculatedWidth = 1;
    } else {
      this.calculatedWidth = this.branchs.reduce((w, currentBranch) => (w + currentBranch.width), 0);
    }

    return this.calculatedWidth;
  }

  // Lazy evaluation
  get height() {
    if (this.calculatedHeight !== -1) {
      return this.calculatedHeight;
    }

    if (this.branchs.length === 0) {
      this.calculatedHeight = 1;
    }
    else {
      let highestBranch = 0;
      this.branchs.forEach(branch => {
        if (branch.height > highestBranch) {
          highestBranch = branch.height;
        }
      });
      this.calculatedHeight = highestBranch + 1;
    }

    return this.calculatedHeight;
  }
}


class MessageTreeNode extends TreeNode {
  constructor(title = '', message = '', type = MESSAGE.ANSWER, branchs = []) {
    super({
      type,
      title,
      message
    }, branchs);
  }

  get type() {
    return this.data.type;
  }

  get title() {
    return this.data.title;
  }
  set title(newTitle) {
    this.data.title = newTitle;
  }

  get message() {
    return this.data.message;
  }
  set message(newMessage) {
    this.data.message = newMessage;
  }
}

export {
  MessageTreeNode
};


