import React from 'react';

import { branchLength, branchAngle } from '../helpers/drawing';
import { COLORS, LINES } from '../helpers/constants';

const styles = {
  container: {
    height: 0,
    width: 3,
    position: 'relative',
    zIndex: -1
  },
  line : {
    width: LINES.branchWidth,
    backgroundColor: COLORS.black,
    position: 'absolute',
    bottom: 0
  }
}

function lineStyle(position) {
  return {
    ...styles.line,
    height: branchLength(position) + 5, // Add 5px
    transformOrigin: 'bottom',
    transform: `rotate(${branchAngle(position)}deg) translateY(4px)`
    // and translate  4px under the message box (1px for a nice spike)
  }
}

const Branch = props => {
  return (
    <div style={styles.container}>
      <div style={lineStyle(props.position)}></div>
    </div>
  );
};

export default Branch;


