import React, { Component } from "react";

import MessageActions from './MessageActions';
import Branch from './Branch';

import { CARD, COLORS, LINES } from '../helpers/constants';

const styles = {
  container: {
    fontSize: 12,
    width: CARD.boxWidth,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  body: {
    backgroundColor: COLORS.white,
    width: '100%',
    cursor: 'pointer',
    overflowWrap: 'break-word',
    border: LINES.border,
    borderRadius: CARD.radius
  },
  titleDiv: {
    borderBottom: LINES.border,
    fontSize: 'larger',
    padding: 15
  },
  titleTextarea: {
    textAlign: 'center',
    boxSizing: 'border-box',
    border: 'none',
    borderBottom: LINES.border,
    fontSize: 'larger',
    resize: 'none',
    width: '100%',
    padding: 15,
    backgroundColor: 'transparent'
  },
  textDiv: {
    textAlign: 'justify',
    padding: 15
  },
  textTextarea: {
    textAlign: 'justify',
    boxSizing: 'border-box',
    border: 'none',
    width: '100%',
    height: 200,
    resize: 'none',
    padding: 15,
    backgroundColor: 'transparent'
  },
  tools: {
    marginTop: 5
  }
};

class Message extends Component {
  constructor(props) {
    super();
    this.state = {
      text: props.text,
      title: props.title,
      editingText: false,
      editingTitle: false
    };

    this.editTitle = this.editTitle.bind(this);
    this.saveTitle = this.saveTitle.bind(this);
    this.editText = this.editText.bind(this);
    this.saveText = this.saveText.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  componentWillReceiveProps({ text, title }) {
    this.setState({
      text,
      title
    });
  }

  editTitle() {
    this.setState({
      editingText: false,
      editingTitle: true
    });
  }

  saveTitle() {
    this.setState({
      editingText: false,
      editingTitle: false
    });
  }

  editText() {
    this.setState({
      editingText: true,
      editingTitle: false
    });
  }

  saveText() {
    this.setState({
      editingText: false,
      editingTitle: false
    });
  }

  handleChange(event) {
    const t = event.target;
    this.setState({
      [t.name]: t.value
    });
  }

  render() {
    return (
      <div style={{...styles.container, ...this.props.style}}>
        <Branch position={this.props.position}/>
        <div
          style={{ ...styles.body, backgroundColor: this.props.color }}
          className="tooltip"
        >
          {/* Title */}
          {this.state.editingTitle ?
            <textarea
              name='title'
              onBlur={this.saveTitle}
              onChange={this.handleChange}
              style={styles.titleTextarea}
              value={this.state.title}
              autoFocus
            >
            </textarea>
            :<div
              onDoubleClick={this.editTitle}
              style={styles.titleDiv}
            >
              {this.state.title}
            </div>
          }
          {/* Message */}
          {this.state.editingText ?
            <textarea
              name='text'
              onBlur={this.saveText}
              onChange={this.handleChange}
              style={styles.textTextarea}
              value={this.state.text}
              autoFocus
            ></textarea>
            :<div
              onDoubleClick={this.editText}
              style={styles.textDiv}
            >
              {this.state.text}
            </div>
          }
          {/* Tooltip */}
          { Object.hasOwnProperty.call(this.props, 'tooltip') &&
            <span className='tooltiptext'>{this.props.tooltip}</span>
          }
        </div>
        <div style={styles.tools}>
          <MessageActions add={this.props.addChild} />
        </div>
      </div>
    );
  }
}

export default Message;



